    margin: 20px;
    width: 300px;
    height: 300px;
    background-color: blue;
    border: 2px solid black;
    color: white;
}

.False {
    display: inline-block;
    margin: 20px;
    width: 300px;
    height: 300px;
    background-color: red;
    border: 2px solid black;
    color: white;
}